<template>
    <transition name="msgbox">
      <div v-if="show" id="MonitorChartWindow" class="msgbox-container" :class="className" @mousedown="mousedown" >
        <table class="headerClass">
            <td style="padding-left:20px">{{'水质点:'+title2}}</td>
            <el-row style="padding-left:5px;text-align:right">
                <img src="../../../static/assets/windowClose.png" style="cursor:pointer;height:29px;width:34px" title="关闭"  v-on:click="cancelBtn">
            </el-row>
        </table>
        <div class="levelClassMy" style="padding-top:20px">
          <div class="levelClassMy" style="padding-left:120px;padding-right:50px">
            <my-date-pick  v-on:myChange='myStartTimeChange' class="startDateMy" :current='this.startSelectDate'></my-date-pick>
          </div>
          <div class="levelClassMy">
            <el-button size="mini" type="success" v-on:click="ppmClick">余氯</el-button>
            <el-button size="mini" type="success" v-on:click="turbidityClick">浊度</el-button>
            <el-button size="mini" type="success" v-on:click="pHClick">pH</el-button>
          </div>
        </div>
        <div class="monitorChartClass">
            <div id="myMonitorChartDiv1" class="monitorChartClass1">  </div>
        </div>
      </div>
    </transition>
</template>
<script>
import MyDatePick from "@/components/DatePicker/MyDatePicker";
import ElementChangeItemRenderer from "@/components/ElementChangeItemRenderer";
import WindowsEvent from "@/components/js/WindowsEvent";
import Bus from "@/bus.js";
export default {
  components: {
    ElementChangeItemRenderer,
    MyDatePick
  },
  data() {
    return {
      startDate:new Date(),
      startSelectDate: {
        year: 
          new Date().getFullYear(),
        month: 
          new Date().getMonth(),
        date: new Date().getDate()
      },
       chartData: {
        listData: {
          listX: [1, 2, 3, 4, 5, 6, 7] ,//x轴数据
          listXNew: [1, 2, 3, 4, 5, 6, 7] ,//x轴数据
        },
        legendLabel: ["模拟值"], //图例的标签
        xAxisName: "时间", //x轴标签
        yAxisName: "模拟值", //y轴标签
        series: [
          {
             
            type: "line",
            name: "模拟值",
            data: [4, 5, 78, 2, 45, 7, 9],
            itemStyle: {
              normal: {
                color: "#5d77e6",
                width: 2
              }
            },
          },
           
        ]
      },
      show: false,
      title: "提示",
      title2: "",
      unit:"",
      content: "",
      confirm: "确定",
      cancel: "",
      className: "",
      listData: [],
      selectElement: "",
      dialogVisible: false,
      selectPoint: false,
      selectLine: false,
      chartType:'Ppm',
      PHCodeId:'',
      YLCodeId:'',
      ZDCodeId:''

    };
  },
  mounted() {
    
    window.addEventListener("event_name", this.myEventListener);
  },
  watch: {
    show(val) {
      if (val) {
        if (!this.hasClass(document.body, "pop-mask")) {
          this.addClass(document.body, "pop-mask");
        }
      } else {
        this.removeClass(document.body, "pop-mask");
      }
    }
  },
  beforeMount() {
    //如果已经存在，则阻止出现第二次
    let node = document.querySelector(".msgbox-container");
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
    }
  },
  methods: {
      ppmClick(){
        this.chartType = "Ppm";
        this.title = "余氯";
        this.unit = 'mg/L';
        var obj = {Type:this.chartType,CodeId:this.YLCodeId,Year:this.startDate.getFullYear(),
        Month:this.startDate.getMonth()+1,Day:this.startDate.getDate()};
        this.content = this.YLCodeId;
        if(this.content!='')
        WindowsEvent.MyDispatchEvent("WaterQualityDataChange",obj);
         else
        this.DrawNoChart();
      },
      turbidityClick(){
        this.chartType = "Turbidity";
        this.title = "浊度";
        this.unit = 'NTU';
        var obj = {Type:this.chartType,CodeId:this.ZDCodeId,Year:this.startDate.getFullYear(),
        Month:this.startDate.getMonth()+1,Day:this.startDate.getDate()};
        this.content = this.ZDCodeId;
        if(this.content!='')
        WindowsEvent.MyDispatchEvent("WaterQualityDataChange",obj);
        else
        this.DrawNoChart();

      },
      DrawNoChart(){
          this.chartData.series[0].data = [];
          this.chartData.listData.listX = [];
          this.chartData.listData.listXNew = [];
          this.chartData.yAxisName = this.title+'('+this.unit+')';
          this.chartData.legendLabel=[this.title], //图例的标签
          this.$nextTick(this.drawLine());
      },
      pHClick(){
        this.chartType = "pH";
        this.title = "pH";
        this.unit = '';
        var obj = {Type:this.chartType,CodeId:this.PHCodeId,Year:this.startDate.getFullYear(),
        Month:this.startDate.getMonth()+1,Day:this.startDate.getDate()};
        this.content = this.PHCodeId;
        if(this.content!='')
        WindowsEvent.MyDispatchEvent("WaterQualityDataChange",obj);
        else
        this.DrawNoChart();
      },
      myStartTimeChange(event){
        this.startDate = event;
        console.log(this.startDate)
        var obj = {Type:this.chartType,CodeId:this.content,Year:this.startDate.getFullYear(),
        Month:this.startDate.getMonth()+1,Day:this.startDate.getDate()};
        WindowsEvent.MyDispatchEvent("WaterQualityDataChange",obj);
      },
      drawLine() {
        var self = this;
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("myMonitorChartDiv1"));
      // 绘制图表
      var option = {
        tooltip: {
          trigger: "none",
          axisPointer: {
            type: "cross",
           /*  crossStyle: {
              color: "#999"
            } */
          },
          ///alwaysShowContent: false,
          //triggerOn: "click"
        },
        grid: {
          height: 240,
          width: 500,
          top: 50,
          left: 60,
          bottom:20
        } ,
        title: {
          textStyle: {
            //文字颜色
            color: "#6e7b8b",
            //字体风格,'normal','italic','oblique'
            fontStyle: "normal",
            //字体粗细 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
            fontWeight: "bold",
            //字体系列
            fontFamily: "sans-serif",
            //字体大小
            fontSize: 14
          },
          left: "center"
        },
        legend: {
          data: this.chartData.legendLabel
        },

         textStyle: {
          color: "#6e7b8b"
        }, 
        xAxis: [
           {
            show:true,
            name: this.chartData.xAxisName,
            data: this.chartData.listData.listXNew,
            axisPointer: {
               label: {
                    formatter: function (params) {
                        return "时间:" + params.value +'\n\r'
                            + self.title+'值：' + params.seriesData[0].data+' '+self.unit;
                    }
                } 
            },
            axisTick: { alignWithLabel: true },
            axisLine: {
              lineStyle: {
                //type: "solid",
                color: "#5d77e6", //左边线的颜色
                //width: "1" //坐标线的宽度
              }
            },
            axisLabel: {
             formatter:function(params) {
               var newParamsName=String(params).split(' ')[1].split(':')[0]
               return newParamsName
             },
             interval:200,
            }
          },
           /* {
            show:true,
            position:'bottom',
            name: this.chartData.xAxisName,
            data: this.chartData.listData.listX,
            axisTick: { alignWithLabel: true },
            axisLine: {
               lineStyle: {
                 color: "#5d77e6", //左边线的颜色
               }
             },
             interval:3, 
             min:0,
          }, */
          
        ],
        yAxis: [
          {
            name: this.chartData.yAxisName,
            type: "value",
             axisLabel: {
              formatter: "{value}"
            }, 
            axisTick: { show: false },
            splitLine: { show: true }, //去除网格线
            axisLine: {
              lineStyle: {
                type: "solid",
                color: "#e2e5e8", //左边线的颜色
                width: "1" //坐标线的宽度
              }
            },
            position: "left",
            min:'dataMin',
            max:'dataMax',
            //max:120,
          }
        ],
        series:this.chartData.series
      };

      myChart.setOption(option);
      myChart.on("click", function(params) {
        // 控制台打印数据的名称
      });
    } ,
    myEventListener(event) {
      var data = event.detail.data;
      var typeMy = event.detail.type;
      if(typeMy=="WaterQualityChartData"){
          this.chartData.series[0].data = data.Value;
          this.chartData.listData.listX = data.TimeList;
          this.chartData.listData.listXNew = data.Time;
          this.chartData.yAxisName = this.title+'('+this.unit+')';
          this.chartData.legendLabel=[this.title], //图例的标签
          this.$nextTick(this.drawLine());
      }
      else if(typeMy=="CloseResultChart"){
        window.removeEventListener("event_name", this.myEventListener);
        this.show = false;
      }
    },
    closeDialog(e) {
      this.dialogVisible = false;
    },
    mousedown(event) {
      this.selectElement = document.getElementById("MonitorChartWindow");
      {
        var div1 = this.selectElement;
        this.isDowm = true;
        var distanceX = event.clientX - this.selectElement.offsetLeft;
        var distanceY = event.clientY - this.selectElement.offsetTop;
        document.onmousemove = function(ev) {
          var oevent = ev || event;
          div1.style.left = oevent.clientX - distanceX + "px";
          div1.style.top = oevent.clientY - distanceY + "px";
        };
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        };
      }
    },
    successBtn() {
      if (this.listData.length != 0) {
        var myEvent = new CustomEvent("event_name", {
          detail: { type: "This is title!" }
        });
        myEvent.detail.type = "ElementChangeList";
        myEvent.detail.data = this.listData;
        if (window.dispatchEvent) {
          window.dispatchEvent(myEvent);
        } else {
          window.fireEvent(myEvent);
        }
      } else {
        this.myAlert("请先添加修改内容", "提示");
      }
    },
    myAlert: function(content, title) {
      this.$alert(content, title, {
        confirmButtonText: "确定"
      });
    },
    cancelBtn() {
      this.show = false;
    },
    addClass(obj, cls) {
      var obj_class = obj.className,
        blank = obj_class != "" ? " " : "";
      var added = obj_class + blank + cls;
      obj.className = added;
    },
    removeClass(obj, cls) {
      var obj_class = " " + obj.className + " ";
      obj_class = obj_class.replace(/(\s+)/gi, " ");
      var removed = obj_class.replace(" " + cls + " ", " ");
      removed = removed.replace(/(^\s+)|(\s+$)/g, "");
      obj.className = removed; //替换原来的 class.
    },
    hasClass(obj, cls) {
      var obj_class = obj.className,
        obj_class_lst = obj_class.split(/\s+/);
      var x = 0;
      for (x in obj_class_lst) {
        if (obj_class_lst[x] == cls) {
          return true;
        }
      }
      return false;
    },
    selectItem(myItem) {
      alert(myItem.ElementId);
    }
  }
};
</script>
<style lang="scss" scoped>
.startDateMy {
  width: 130px;
   z-index: 99;
}
.levelClassMy{
    display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: row;
}
.monitorChartClass{
    height: 350px;
    width: 650px;
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
}
.monitorChartClass1 {
   height: 350px; 
   width: 650px;
}
.colClass {
  width: 18px;
}
.tableClass {
 width: 100%;
   /*  max-height: 160px;
    overflow: auto; */

}
.headerClass {
  vertical-align: middle;
  height: 40px;
  width: 100%;
  font-size: 13px;
  background: #f5f8f8;
}
.msgbox-container {
  z-index: 999;
/* height: 380px; */

  position: absolute;
  top: 57%;
  left: 50%;
/*   width: 30%; */
  background: #fff;
  color: #000;
  transform: translate(-50%, -50%) scale(1, 1);
}
.footer {
  width: 100%;
  height: 64px;
  text-align: right;
  vertical-align: middle;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}
.msgbox-enter,
.msgbox-leave-to {
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
}
.msgbox-enter-active,
.msgbox-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.msgbox-enter-to,
.msgbox-leave {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
}
.drawToolClass {
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>



