<template>
    <transition name="msgbox">
      <div v-if="show" id="myWindow" class="msgbox-container" :class="className" @mousedown="mousedown" >
         <table class="headerClass">
            <td style="padding-left:20px">保存截图信息</td>
            <el-row style="padding-left:5px;text-align:right">
                <img src="../../../../static/assets/windowClose.png" style="cursor:pointer;height:29px;width:34px" title="关闭"  v-on:click="cancelBtn">
            </el-row>
          </table>
          <div>
            <div class="div1" style="padding-top:10px">
              <div>截图类型：</div>
              <div>{{screensType}}</div>
            </div>
            <div class="div1" style="padding-top:10px">
              <div>结果截图：</div>
              <img style="width:250px;height:130px" :src="dataURL" alt="">
            </div>
            <div class="div1" style="padding-top:10px">
              <div>结果描述：</div>
              <el-input rows="5" style="height:100px;width:250px" type="textarea" v-model="resultDescribe"></el-input>
            </div>
          </div>
          <el-row class="footer">
            <el-button size="mini" type="success" @click="successBtn">{{confirm}}</el-button>
          </el-row>
      </div>
    </transition>
</template>
<script>
import ElementChangeItemRenderer from "@/components/ElementChangeItemRenderer";
import WindowsEvent from "@/components/js/WindowsEvent";
export default {
  components: {
    ElementChangeItemRenderer
  },
  data() {
    return {
      show: false,
      title: "提示",
      title2: "",
      content: "",
      confirm: "确定",
      cancel: "",
      className: "",
      listData: [],
      selectElement: "",
      dialogVisible: false,
      selectPoint: false,
      selectLine: false,
      screensType: "",
      dataURL: "",
      resultDescribe: ""
    };
  },
  mounted() {
    window.addEventListener("event_name", this.myEventListener);
  },
  watch: {
    show(val) {
      if (val) {
        if (!this.hasClass(document.body, "pop-mask")) {
          this.addClass(document.body, "pop-mask");
        }
      } else {
        this.removeClass(document.body, "pop-mask");
      }
    }
  },
  beforeMount() {
    //如果已经存在，则阻止出现第二次
    let node = document.querySelector(".msgbox-container");
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
    }
  },
  methods: {
    myEventListener(event) {
      var type = event.detail.eventType;
      var data = event.detail.time;
      var typeMy = event.detail.type;
      if (type == "updateTime") {
        this.title2 = data;
      } else if (typeMy == "DrawFinish") {
        this.selectPoint = this.selectLine = false;
      } /* else if (typeMy == "SelectElementListChange") {
        this.listData = event.detail.data;
      } */else if(typeMy=="ScreenDialogClose"){
        this.show = false;
      }
    },
    DrawPoint() {
      this.selectPoint = !this.selectPoint;
      if (!this.selectPoint) WindowsEvent.MyDispatchEvent("DrawFinish", "");
      else WindowsEvent.MyDispatchEvent("DrawTool", "point");
    },
    DrawLine() {
      this.selectLine = !this.selectLine;
      if (!this.selectLine) WindowsEvent.MyDispatchEvent("DrawFinish", "");
      else WindowsEvent.MyDispatchEvent("DrawTool", "polyline");
    },
    itemClick(itemData) {
      var myEvent = new CustomEvent("event_name", {
        detail: { type: "This is title!" }
      });
      myEvent.detail.type = "ElementItemClick";
      myEvent.detail.data = itemData;
      if (window.dispatchEvent) {
        window.dispatchEvent(myEvent);
      } else {
        window.fireEvent(myEvent);
      }
    },
    DeleteClick(itemData) {
      var deleteId = itemData.ElementId;
      for (var i = 0; i < this.listData.length; i++) {
        var item = this.listData[i];
        if (item.ElementId == deleteId) {
          this.listData.splice(i, 1);
          break;
        }
      }
      var myEvent = new CustomEvent("event_name", {
        detail: { type: "This is title!" }
      });
      myEvent.detail.type = "ElementChangeListDelete";
      myEvent.detail.data = itemData;
      if (window.dispatchEvent) {
        window.dispatchEvent(myEvent);
      } else {
        window.fireEvent(myEvent);
      }
    },
    closeDialog(e) {
      this.dialogVisible = false;
    },
    mousedown(event) {
      this.selectElement = document.getElementById("myWindow");
      {
        var div1 = this.selectElement;
        this.isDowm = true;
        var distanceX = event.clientX - this.selectElement.offsetLeft;
        var distanceY = event.clientY - this.selectElement.offsetTop;
        document.onmousemove = function(ev) {
          var oevent = ev || event;
          div1.style.left = oevent.clientX - distanceX + "px";
          div1.style.top = oevent.clientY - distanceY + "px";
        };
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        };
      }
    },
    successBtn() {
      var data = {
        ImageSrc: this.dataURL,
        Type: this.screensType,
        ResultDescribe: this.resultDescribe
      };
      WindowsEvent.MyDispatchEvent("PlanResultScreen", data);
      this.show = false;
    },
    myAlert: function(content, title) {
      this.$alert(content, title, {
        confirmButtonText: "确定"
      });
    },
    cancelBtn() {
      this.show = false;
      var myEvent = new CustomEvent("event_name", {
        detail: { type: "This is title!" }
      });
      myEvent.detail.type = "PlanTempWindowClose";
      if (window.dispatchEvent) {
        window.dispatchEvent(myEvent);
      } else {
        window.fireEvent(myEvent);
      }
    },
    addClass(obj, cls) {
      var obj_class = obj.className,
        blank = obj_class != "" ? " " : "";
      var added = obj_class + blank + cls;
      obj.className = added;
    },
    removeClass(obj, cls) {
      var obj_class = " " + obj.className + " ";
      obj_class = obj_class.replace(/(\s+)/gi, " ");
      var removed = obj_class.replace(" " + cls + " ", " ");
      removed = removed.replace(/(^\s+)|(\s+$)/g, "");
      obj.className = removed; //替换原来的 class.
    },
    hasClass(obj, cls) {
      var obj_class = obj.className,
        obj_class_lst = obj_class.split(/\s+/);
      var x = 0;
      for (x in obj_class_lst) {
        if (obj_class_lst[x] == cls) {
          return true;
        }
      }
      return false;
    },
    selectItem(myItem) {
      alert(myItem.ElementId);
    }
  }
};
</script>
<style lang="scss" scoped>
.div1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: row;
  padding-right: 40px;
  padding-left: 40px;
}
.colClass {
  width: 18px;
}
.tableClass {
  /* width: 540px; */
  width: 100%;
}
.headerClass {
  vertical-align: middle;
  height: 40px;
  width: 100%;
  font-size: 13px;
  background: #f5f8f8;
}
.msgbox-container {
  z-index: 2;
  max-height: 380px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 400px;
  background: #fff;
  color: #000;
  transform: translate(-50%, -50%) scale(1, 1);
}
.footer {
  width: 100%;
  height: 64px;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}
.msgbox-enter,
.msgbox-leave-to {
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
}
.msgbox-enter-active,
.msgbox-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.msgbox-enter-to,
.msgbox-leave {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
}
.drawToolClass {
  padding-left: 20px;
}
</style>


