import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    closeopenstatus: true,
    dispatchTypeNum: "模拟调度",
    count: 0,
    themecolor: '1b1e24' //默认为20a0ff
  },
  mutations: {
    closeimg(state, data) {
      this.state.closeopenstatus = data
    },
    openimg(state, data) {
      this.state.closeopenstatus = data
    },
    dispatchType(state, data) {
      this.state.dispatchTypeNum = data == "0" ? "模拟调度" : "智能调度"
    },
    //更新主题颜色
    setThemeColor(state, curcolor) {
      this.state.themecolor = curcolor;
    },
    increment: state => state.count++,
    decrement: state => state.count--,
  }
});
export default store;



