<template>
    <transition name="msgbox">
    <el-dialog id="myWindowMonitor" :title="title" :visible.sync="show" :modal-append-to-body="false" 
    :close-on-click-modal="false" :modal="true">
      <!-- <div v-if="show"  class="msgbox-container" :class="className"> -->
       <!--  <table class="headerClass">
            <td style="padding-left:20px">{{title}}</td>
            <el-row style="padding-left:5px;text-align:right">
                <img src="../../assets/windowClose.png" style="cursor:pointer;height:29px;width:34px" title="关闭"  v-on:click="cancelBtn">
            </el-row>
        </table> -->
        <el-tabs style="padding-top:0px" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="压力监测点" name="pressure"></el-tab-pane>
            <el-tab-pane label="流量监测点" name="demand"></el-tab-pane>
        </el-tabs>
        <el-table height="300px" class="tableClass" border :data="listData">
          <el-table-column label="监测点" prop="Label"  :align="'center'"> 
          </el-table-column>
          <el-table-column label="指定不参加统计的监测点"  :align="'center'"> 
              <template slot-scope="scope" style="width:100%">
                  <div>
                      <el-checkbox v-model="scope.row.IsShow"></el-checkbox>
                  </div>
              </template>
          </el-table-column>
        </el-table>
        <el-row class="footer">
        <el-button size="mini" type="success" @click="successBtn">{{confirm}}</el-button>
        </el-row>
      <!-- </div> -->
    </el-dialog>
    </transition>
</template>
<script>
import WindowsEvent from "@/components/js/WindowsEvent";
import axios from 'axios';
import urlClass from "@/components/js/UrlClass";
export default {
  components: {
  },
  data() {
    return {
      activeName:'pressure',
      show: false,
      title: "提示",
      title2: "",
      content: "",
      confirm: "确定",
      cancel: "",
      className: "",
      listData: [],
      selectElement: "",
      dialogVisible: false,
      selectPoint: false,
      selectLine: false,
      pressureList:[],
      demandList:[]
    };
  },
  mounted() {
    this.GetMonitorShowInfo("pressure");
    this.GetMonitorShowInfo("demand");
    window.addEventListener("event_name", this.myEventListener);
  },
  watch: {
    show(val) {
       /* if (val) {
        if (!this.hasClass(document.body, "pop-mask")) {
          this.addClass(document.body, "pop-mask");
        }
      } else {
        this.removeClass(document.body, "pop-mask");
      } */
    } 
  },
  beforeMount() {
    //如果已经存在，则阻止出现第二次
    let node = document.querySelector(".msgbox-container");
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
    }
  },
  methods:{
      hasClass(obj, cls) {
      var obj_class = obj.className,
        obj_class_lst = obj_class.split(/\s+/);
      var x = 0;
      for (x in obj_class_lst) {
        if (obj_class_lst[x] == cls) {
          return true;
        }
      }
      return false;
    },
      myEventListener(event){

      },
    handleClick(){
        if(this.activeName=="pressure")
        {
            this.demandList = this.listData.slice(0);
            if(this.pressureList.length!=0)
            {
                this.listData = this.pressureList.slice(0);
            }
            else
            {
                this.GetMonitorShowInfo(this.activeName);
                this.listData = this.pressureList.slice(0);
            }
        }
        else
        {
            this.pressureList = this.listData.slice(0);
            if(this.demandList.length!=0)
            {
                this.listData = this.demandList.slice(0);
            }
            else
            {
                this.GetMonitorShowInfo(this.activeName);
                this.listData = this.demandList.slice(0);
            }
        }
    },
    GetMonitorShowInfo(type){
        var self = this;
         axios.post(
          urlClass.axiosUrl + "GetMonitorIsShowInfo",
          JSON.stringify(type),
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(function(respone) {
            if(type=="pressure")
            {
                self.pressureList = respone.data.slice(0);
                if(self.activeName==type)
                self.listData = self.pressureList.slice(0);
            }
            else
            {
                self.demandList = respone.data.slice(0);
                if(self.activeName==type)
                self.listData = self.demandList.slice(0);
            }
           
        })

    },
    SaveMonitorPressureShowInfo(lisTemp){
         axios.post(
          urlClass.axiosUrl + "UpdateMonitorIsShow",
          JSON.stringify(lisTemp),
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(this.SaveMonitorPressureInfo)

    },
    SaveMonitorPressureInfo(respone){
        
    },
    SaveMonitorDemandShowInfo(lisTemp){
         axios.post(
          urlClass.axiosUrl + "UpdateMonitorIsShow",
          JSON.stringify(lisTemp),
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(this.SaveMonitorDemandInfo)

    },
    SaveMonitorDemandInfo(respone){
        this.show = false;
        WindowsEvent.MyDispatchEvent("AccuracyUpdate", "");
    },
    cancelBtn() {
      this.show = false;
    },
    successBtn(){
        var pressureListTemp = {
            Type:"pressure",
            MonitorList:this.pressureList
        }
        this.SaveMonitorPressureShowInfo(pressureListTemp);
        var demandListTemp = {
            Type:"demand",
            MonitorList:this.demandList
        }
        this.SaveMonitorDemandShowInfo(demandListTemp);
    },
    mousedown(event) {
      this.selectElement = document.getElementById("myWindowMonitor");
      {
        var div1 = this.selectElement;
        this.isDowm = true;
        var distanceX = event.clientX - this.selectElement.offsetLeft;
        var distanceY = event.clientY - this.selectElement.offsetTop;
        document.onmousemove = function(ev) {
          var oevent = ev || event;
          div1.style.left = oevent.clientX - distanceX + "px";
          div1.style.top = oevent.clientY - distanceY + "px";
        };
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        };
      }
    },
  }
};
</script>
<style>
  .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #58a632;
    border-color: #58a632;
}
.el-checkbox__inner:hover {
    border-color: #58a632;
}
.el-button--success {
    color: #fff;
    background-color: #58a632;
    border-color: #58a632;
}
</style>
<style lang="scss" scoped>
.colClass {
  width: 18px;
}
.tableClass {
  width: 100%;
}
.headerClass {
  vertical-align: middle;
  height: 40px;
  width: 100%;
  font-size: 13px;
  background: #f5f8f8;
}
.msgbox-container {
  max-height: 380px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40%;
  background: #fff;
  color: #000;
  transform: translate(-50%, -50%) scale(1, 1);
}
.footer {
  width: 100%;
  height: 64px;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}
.msgbox-enter,
.msgbox-leave-to {
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
}
.msgbox-enter-active,
.msgbox-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.msgbox-enter-to,
.msgbox-leave {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
}
.drawToolClass {
  padding-left: 20px;
}
</style>


