<template>
    <transition name="msgbox">
    <el-dialog id="UserManagerWindow" :title="title" :visible.sync="show" :modal-append-to-body="false" 
    :close-on-click-modal="false" :modal="true">
        <el-button type="success" size="mini" v-on:click="AddUser">添加用户</el-button>
        <el-table height="300px" class="tableClass" border :data="listData" style="top:10px">
          <el-table-column label="用户名" prop="UserName"  :align="'center'"> 
          </el-table-column>
          <el-table-column label="姓名" prop="Name" :align="'center'"> 
          </el-table-column>
           <el-table-column label="部门" prop="Department" :align="'center'"> 
          </el-table-column>
           <el-table-column label="职位" prop="Post" :align="'center'"> 
          </el-table-column>
           <el-table-column label="用户权限" prop="JurisdictionNameStr" :align="'center'"> 
          </el-table-column>
          <el-table-column label="编辑" :align="'center'"> 
              <template slot-scope="scope">
                <img title="编辑" style="padding-top:4px" class="imgClass" v-on:click="Editor(scope.row)" src="../../../static/assets/planManager/edit_16x16.png" alt="">
              </template>
          </el-table-column>
          <el-table-column label="删除" :align="'center'"> 
              <template slot-scope="scope">
                <img title="删除" v-on:click="Delete(scope.row)" class="imgClass" style="padding-top:4px" src="../../../static/assets/planManager/delete_16x16.png" alt="">
              </template>
          </el-table-column>
        </el-table>
    </el-dialog>
    </transition>
</template>
<script>
import WindowsEvent from "@/components/js/WindowsEvent";
import axios from 'axios';
import urlClass from "@/components/js/UrlClass";
export default {
  components: {
  },
  data() {
    return {
      activeName:'pressure',
      show: false,
      title: "提示",
      title2: "",
      content: "",
      confirm: "确定",
      cancel: "",
      className: "",
      listData: [],
      selectElement: "",
      dialogVisible: false,
      selectPoint: false,
      selectLine: false,
      pressureList:[],
      demandList:[],
      GetUserInfoReturn:'',
      ListJurisdictionName:[],
    };
  },
  mounted() {
      this.GetAllUserInfo();
    window.addEventListener("event_name", this.myEventListener);
  },
  watch: {
    show(val) {
    } 
  },
  beforeMount() {
    //如果已经存在，则阻止出现第二次
    let node = document.querySelector(".msgbox-container");
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
    }
  },
  methods:{
      GetAllUserInfo(){
        var self = this;
         axios.post(
          urlClass.axiosUrl + "UserManageData",
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(function(respone) {
            self.GetUserInfoReturn = Object.assign({},respone.data);
            self.listData = self.GetUserInfoReturn.UserList.slice(0);
        })
      },
      Editor(userInfo){
          this.ListJurisdictionName = [];
          for(var i=0;i<this.GetUserInfoReturn.ListJurisdictionName.length;i++){
              var obj = this.GetUserInfoReturn.ListJurisdictionName[i];
              var object = new Object();
              object.Label = obj;
              object.Select = false;
              if(userInfo.JurisdictionNameStr!=''){
                   for(var j=0;j<userInfo.JurisdictionName.length;j++){
                    var objTemp = userInfo.JurisdictionName[j];
                    if(String(obj)==String(objTemp)){
                        object.Select = true;
                        break;
                  }
                }
              }
             
              this.ListJurisdictionName.push(object);
          }
          userInfo.PasswordDouble = userInfo.Password;
          this.UserAddWindow(userInfo,userInfo.UserId);
      },
      Delete(userInfo){
          var self = this;
          axios.post(
          urlClass.axiosUrl + "DeleteUser",
          JSON.stringify(userInfo.UserId),
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(function(respone) {
            self.GetAllUserInfo();
            alert('删除成功');
        })
      },
      AddUser(){
          this.ListJurisdictionName = [];
          for(var i=0;i<this.GetUserInfoReturn.ListJurisdictionName.length;i++){
              var obj = new Object(); 
              obj.Id = i+1;
              obj.Select = true;
              obj.Label = this.GetUserInfoReturn.ListJurisdictionName[i];
              this.ListJurisdictionName.push(obj);
          }
          var userInfoTemp = {
              UserName:'',
              Name:'',
              Password:'',
              Department:'',
              Mobilephone:'',
              Post:''
          }
          this.UserAddWindow(userInfoTemp,'');
      },
        UserAddWindow: function(userInfo,userId) {
            this.$UserAddWindow({
                title: "用户设置管理",
                cancel: "取消",
                content: "",
                confirm: "确定修改",
                className: "pop-custom",
                listData:this.ListJurisdictionName,
                userInfo:userInfo,
                userId:userId
            })
            .then(() => {
            })
            .catch(err => {
            console.log("error");
            });
    },
      hasClass(obj, cls) {
      var obj_class = obj.className,
        obj_class_lst = obj_class.split(/\s+/);
      var x = 0;
      for (x in obj_class_lst) {
        if (obj_class_lst[x] == cls) {
          return true;
        }
      }
      return false;
    },
      myEventListener(event){
        var typeMy = event.detail.type;
        var selectElement = event.detail.data;
        if(typeMy=="UserAdd"){
            this.GetAllUserInfo();
        }
      },
    mousedown(event) {
      this.selectElement = document.getElementById("myWindowMonitor");
      {
        var div1 = this.selectElement;
        this.isDowm = true;
        var distanceX = event.clientX - this.selectElement.offsetLeft;
        var distanceY = event.clientY - this.selectElement.offsetTop;
        document.onmousemove = function(ev) {
          var oevent = ev || event;
          div1.style.left = oevent.clientX - distanceX + "px";
          div1.style.top = oevent.clientY - distanceY + "px";
        };
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        };
      }
    },
  }
};
</script>
<style>
  .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #58a632;
    border-color: #58a632;
}
.el-checkbox__inner:hover {
    border-color: #58a632;
}
.el-button--success {
    color: #fff;
    background-color: #58a632;
    border-color: #58a632;
}
</style>
<style lang="scss" scoped>
.imgClass {
  cursor: pointer;
}
.colClass {
  width: 18px;
}
.tableClass {
  width: 100%;
}
.headerClass {
  vertical-align: middle;
  height: 40px;
  width: 100%;
  font-size: 13px;
  background: #f5f8f8;
}
.msgbox-container {
  max-height: 380px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40%;
  background: #fff;
  color: #000;
  transform: translate(-50%, -50%) scale(1, 1);
}
.footer {
  width: 100%;
  height: 64px;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}
.msgbox-enter,
.msgbox-leave-to {
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
}
.msgbox-enter-active,
.msgbox-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.msgbox-enter-to,
.msgbox-leave {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
}
.drawToolClass {
  padding-left: 20px;
}
</style>


