import diffusionChart from '@/components/DiffusionNew/MonitorChart.vue'

let DiffusionChart = (options={}) => {
  let msgBoxConstructor = Vue.extend(diffusionChart);
  let instance= new msgBoxConstructor({
      el: document.createElement('div')
  });
  document.body.appendChild(instance.$el);
  // 将单个 confirm instance 的配置合并到默认值中
  Object.assign(instance.$data, options);
  return new Promise((resolve, reject)=>{
      instance.show = true;
      let successBtn = instance.successBtn;
      let cancelBtn = instance.cancelBtn;
      instance.successBtn = () => {
        resolve();
        successBtn();
      }
      instance.cancelBtn = () => {
        reject();
        cancelBtn();
      }
  });
}

