<template>
    <transition name="msgbox">
    <el-dialog id="UserAddWindow" :title="title" :visible.sync="show" :modal-append-to-body="false" 
        :close-on-click-modal="false" :modal="true">
        <div class="linerighttext">用户数据以及权限的设置录入</div>
            <div class="divLevelClass">
                <div>用户权限：</div>
                <div class="divLevelClass">
                    <div v-for="item in listData" :key="item.index">
                        <el-checkbox style="padding-right:10px" v-model="item.Select">{{item.Label}}</el-checkbox>
                    </div>
                </div>
            </div>
              <div class="divLevelClass MyUserLevelClassHCenter">
                    <div style="width:80px">用户名：</div>
                    <el-input v-model="userInfo.UserName"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">密码：</div>
                    <el-input v-model="userInfo.Password"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">确认密码：</div>
                    <el-input v-model="userInfo.PasswordDouble"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">姓名：</div>
                    <el-input v-model="userInfo.Name"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">所在部门：</div>
                    <el-input v-model="userInfo.Post"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">担任职务：</div>
                    <el-input v-model="userInfo.Department"></el-input>
                </div>
                <div class="divLevelClass MyUserLevelClassHCenter">
                     <div style="width:80px">联系电话：</div>
                    <el-input v-model="userInfo.Mobilephone"></el-input>
                </div>
                <div class="divLevelClass" style="padding-top:10px;width:100%;justify-content:center;">
                    <el-button type="success" size="mini" v-on:click="Save">保存</el-button>
                </div>
    </el-dialog>
    </transition>
</template>
<script>
import WindowsEvent from "@/components/js/WindowsEvent";
import axios from 'axios';
import urlClass from "@/components/js/UrlClass";
export default {
  components: {
  },
  data() {
    return {
        userName:'',
        password:'',
        passwordDouble:'',
        name:'',
        post:'',
        department:'',
        phone:'',
      activeName:'pressure',
      show: false,
      title: "提示",
      title2: "",
      content: "",
      confirm: "确定",
      cancel: "",
      className: "",
      listData: [],
      userInfo:'',
      selectElement: "",
      dialogVisible: false,
      selectPoint: false,
      selectLine: false,
      pressureList:[],
      demandList:[],
      GetUserInfoReturn:'',
      userId:''
    };
  },
  mounted() {
    window.addEventListener("event_name", this.myEventListener);
  },
  watch: {
    show(val) {
    } 
  },
  beforeMount() {
    //如果已经存在，则阻止出现第二次
    let node = document.querySelector(".msgbox-container");
    if (node && node.parentNode) {
      node.parentNode.removeChild(node);
    }
  },
  methods:{
      Save(){
          var ids = new Array();
          var names = new Array();
          for(var i=0;i<this.listData.length;i++){
              if(this.listData[i].Select){
                ids.push(i+1);
                names.push(this.listData[i].Label);
              }
          }
          this.userInfo.Jurisdiction = ids;
          this.userInfo.JurisdictionName = names;
          var self = this;
          axios.post(
          urlClass.axiosUrl + "AddUpdateUser",
          JSON.stringify(this.userInfo),
          { headers: { "Content-Type": "application/json;" } }
        )
        .then(function(respone) {
            self.show = false;
            WindowsEvent.MyDispatchEvent("UserAdd", '');
            alert('保存成功');
        })
      },
      hasClass(obj, cls) {
      var obj_class = obj.className,
        obj_class_lst = obj_class.split(/\s+/);
      var x = 0;
      for (x in obj_class_lst) {
        if (obj_class_lst[x] == cls) {
          return true;
        }
      }
      return false;
    },
      myEventListener(event){

      },
    mousedown(event) {
      this.selectElement = document.getElementById("myWindowMonitor");
      {
        var div1 = this.selectElement;
        this.isDowm = true;
        var distanceX = event.clientX - this.selectElement.offsetLeft;
        var distanceY = event.clientY - this.selectElement.offsetTop;
        document.onmousemove = function(ev) {
          var oevent = ev || event;
          div1.style.left = oevent.clientX - distanceX + "px";
          div1.style.top = oevent.clientY - distanceY + "px";
        };
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        };
      }
    },
  }
};
</script>
<style>

.el-select .el-input.is-focus .el-input__inner{
  border-color: #58a632;
}
.el-select .el-input__inner:focus{
  border-color: #58a632;
}
.el-checkbox__input.is-checked+.el-checkbox__label{
  color: #58a632;
}
.el-checkbox__input.is-focus .el-checkbox__inner{
  border-color: #58a632;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #58a632;
    border-color: #58a632;
}
.el-checkbox__inner:hover {
    border-color: #58a632;
}
.el-button--success {
    color: #fff;
    background-color: #58a632;
    border-color: #58a632;
}
</style>
<style lang="scss" scoped>
.MyUserLevelClassHCenter{
  padding-top: 10px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;/*垂直居中*/
}
.divLevelClass {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.linerighttext {
  height: 39px;
  line-height: 39px;
  font-family: "微软雅黑";
  font-size: 14px;
  color: #6e7b8b;
  font-weight: bold;
}
.imgClass {
  cursor: pointer;
}
.colClass {
  width: 18px;
}
.tableClass {
  width: 100%;
}
.headerClass {
  vertical-align: middle;
  height: 40px;
  width: 100%;
  font-size: 13px;
  background: #f5f8f8;
}
.msgbox-container {
  max-height: 380px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40%;
  background: #fff;
  color: #000;
  transform: translate(-50%, -50%) scale(1, 1);
}
.footer {
  width: 100%;
  height: 64px;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}
.msgbox-enter,
.msgbox-leave-to {
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
}
.msgbox-enter-active,
.msgbox-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.msgbox-enter-to,
.msgbox-leave {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
}
.drawToolClass {
  padding-left: 20px;
}
</style>


